// export default Slider;



import React, { useRef, useState, useEffect } from 'react';
import AOS from 'aos';
import 'aos/dist/aos.css';
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';
import 'swiper/css/effect-coverflow';
import 'swiper/css/pagination';
import { EffectCoverflow, Pagination, Navigation } from 'swiper/modules';

import Illustration5 from '../img/Illustration5.png';
import Illustration5a from '../img/Illustration5a.png';
import Illustration5b from '../img/Illustration5b.png';
import Illustration5c from '../img/Illustration5c.png';
import Illustration5d from '../img/Illustration5d.png';
import Illustration5e from '../img/Illustration5e.png';



function MySlider() {

    useEffect(() => {
        AOS.init({
            duration: 1500, 
        });

        AOS.refresh(); 
    }, []);

  return (
    <>
      <Swiper
      effect={'coverflow'}
      grabCursor={true}
      centeredSlides={true}
      slidesPerView={'auto'}
      coverflowEffect={{
        rotate: 50,
        stretch: 0,
        depth: 200,
        modifier: 2,
        slideShadows: true,
      }}
      pagination={{
        el: ".swiper-pagination",
        clickable: true
      }}
      spaceBetween={30} 
      loop={true} 
      breakpoints={{
        0: {
          slidesPerView: 1,
        },
        400: {
          slidesPerView: 1,
        },
        768: {
          slidesPerView: 2,
        },
      }}
      className="mySwiper p-4" data-aos="fade"
    >
        <SwiperSlide>
          <img src={Illustration5} />
          <h5 className='py-3 ms-4 rouga fs-5 new-white swipe-cap'>The treasury of Notre Dame Cathedral</h5>
        </SwiperSlide>
        <SwiperSlide>
          <img src={Illustration5e} />
          <h5 className='py-3 ms-4 rouga fs-5 new-white swipe-cap'>Greece: Exhibition of Portraits</h5>

        </SwiperSlide>
        <SwiperSlide>
          <img src={Illustration5a} />
          <h5 className='py-3 ms-4 rouga fs-5 new-white swipe-cap'>The Pavillon des Sessions</h5>
        </SwiperSlide>
       
        <SwiperSlide>
          <img src={Illustration5c} />
          <h5 className='py-3 ms-4 rouga fs-5 new-white swipe-cap'>Samuel F.B.More's Gallery of the Louvre</h5>
        </SwiperSlide>
        <SwiperSlide>
          <img src={Illustration5d} />
          <h5 className='py-3 ms-4 rouga fs-5 new-white swipe-cap'>The Power of Spaces</h5>
        </SwiperSlide>
        <SwiperSlide>
          <img src={Illustration5b} />
          <h5 className='py-3 ms-4 rouga fs-5 new-white swipe-cap'>Uzbekistan showcases its rich culture</h5>
        </SwiperSlide>
     
      </Swiper>
    </>
  );
}

export default MySlider;