import React, { useEffect } from 'react';
import AOS from 'aos';
import 'aos/dist/aos.css';

function Section1() {
    useEffect(() => {
        AOS.init({
            duration: 1500, 
        });

        AOS.refresh(); 
    }, []);

    return (
        <div name="section1" className="section1 text-light d-flex align-items-center">
            <div className="animation pt-5" data-aos="fade-down" >
                <h1 className="new-white pt-5 mt-5 display-1 ahgan" >
                    Welcome to the <br />
                    Lou<span className="new-yell sec-title">v</span>re Museum
                </h1>
                <img
                    src="https://cdn-icons-png.flaticon.com/128/1235/1235761.png"
                    className="img-1"
                    style={{ width: '110px', height: '90px', objectFit: 'contain' }}
                />
            </div>
        </div>
    );
}

export default Section1;


