// export default ImageGrid;


import React, { useState } from 'react';
import 'aos/dist/aos.css';
import Modal from 'react-bootstrap/Modal';

const ImageGrid = () => {
  const [showModal, setShowModal] = useState(false);
  const [selectedItem, setSelectedItem] = useState({});

  const handleShowModal = (item) => {
    setSelectedItem(item);
    setShowModal(true);
  };

  const handleCloseModal = () => setShowModal(false);

  const items = [
    {
        imgSrc: "https://www.yonder.fr/sites/default/files/styles/mobile_360x484/public/cityguides/rubriques/restaurants/CGF-NYC-BENOIT-2.jpg?itok=WKKRUSxt",
        title: "BISTROT BENOIT",
        description: "Under the Pyramid, at the heart of the Musée du Louvre, the Bistrot Benoit welcomes guests in a friendly setting for a coffee and croissant at the bar or a meal in the pure Parisian bistrot tradition. ",
        price: "€€€ - Menu costs €31,50",
        location: "Level -2 Under the Pyramid ",
        openingHours: "Open daily from 11:45 am to 5:30 pm, and until 8 p.m. on Fridays (during French school holidays only). Kitchen closes one hour before closing time.",
        className: "large2",
        
    },
    {
        imgSrc: "https://10619-2.s.cdn12.com/reviews/original/702379.jpg",
        title: " LE CAFÉ MOLLIEN",
        description: "Just minutes away from the Mona Lisa, the Café Mollien welcomes you all day long with a selection of sweet and savoury snacks. In summer, a lovely terrace overlooking the Pyramid offers stunning views of the Louvre, Tuileries gardens and the Carrousel du Louvre. Relax and enjoy in this unforgettable setting at the heart of the museum!",
        price: "€€",
        location: "Level 1 Denon wing (next to the red French paintings rooms)",
        openingHours: "Open daily from 9:45 a.m. to 4:45 p.m., and until 6:30 p.m. on Fridays.",
        className: "large2",
    },
    {
        imgSrc: "https://media-cdn.tripadvisor.com/media/photo-s/04/5c/2a/e0/cafe-marly.jpg",
        title: "ANGELINA ",
        description: "Enjoy the breathtaking views of the Louvre, Pyramid and Paris from the  Café Richelieu-Angélina. After visiting the Napoleon III apartments or admiring Charlemagne's sword, savour the house's famous hot chocolate and signature Mont Blanc pastry or other sweet or savoury delights. ",
        price: "€€€",
        location: "Level 1 Richelieu wing (near Napoleon III Apartments)",
        openingHours: "Open from 10 a.m. to 4:45 p.m., and until 6:30 p.m. on Fridays",
    },
    {
        imgSrc: "https://media.timeout.com/images/105781144/750/422/image.jpg",
        title: "TERRASSE DE POMONE",
        description: "Enjoy a delicious meal at the Terrasse de Pomone, an oasis of green in the heart of Paris. People of all ages will delight in the peaceful and charming terrace next to a fountain. Pop in throughout the day for a selection of freshly made salads, savoury tarts, sweet or savoury pancakes, pastries and delicious frozen treats. A takeaway service is also available.",
        price: "€€",
        location: "Tuileries Garden - south-east kiosk Metro: Tuileries and Concorde ",
        openingHours: "Open daily from 10 a.m. to 6 p.m.",
    },
    {
        imgSrc: "https://musiam-paris.com/wp-content/uploads/2020/06/musiam-cafe-richelieu-petit-dejeuner-terrasse-768x512.jpg",
        title: "CAFÉ RICHELIEU",
        description: "A landmark Parisian institution, the Café Richelieu welcomes guests all day long in a unique and intimate setting. Sip a cocktail at sunset, enjoy lunch overlooking the superb sculptures in the Cour de Marly or have brunch while enjoying stunning views of the Pyramid. ",
        price: "€€€€",
        location: "93 rue de Rivoli 75001 Paris Opposite the Pyramid ",
        openingHours: "Open daily with non-stop service from 8 a.m. to 2 a.m. ",
    }

  ];

  return (
     <div className="image-collage2 ">
      {items.map((item, index) => (
        <div key={index} className={`card2 ${item.className}`} style={{ position: 'relative' }} onClick={() => handleShowModal(item)}>
          <img src={item.imgSrc} alt={item.title} className="item2" data-aos="fade" />
          <p className="rouga fs-4" style={{ position: 'absolute', bottom: 0, left: 0, padding: '10px', color: 'white', fontWeight: 'bold' }}>
            {item.title} <br /> <i className="bi bi-plus-lg"></i>
          </p>
        </div>
      ))}

      <Modal show={showModal} onHide={handleCloseModal}   >
        <Modal.Header closeButton className='rouga'> 
          <Modal.Title>{selectedItem.title}</Modal.Title>
        </Modal.Header>
        <Modal.Body className='p-0 m-0'>
          <img className="img-fluid" src={selectedItem.imgSrc} alt={selectedItem.title} style={{width: '500px', height: '450px', objectFit: 'cover'}} />
           <div className='p-4 '>
            <h5 className='stan fw-bold'>PRICE RANGE</h5>
            <p>{selectedItem.price}</p>

            <h5 className='stan fw-bold'>LOCATION</h5>
            <p>{selectedItem.location}</p>

            <h5 className='stan fw-bold'>OPENING HOURS</h5>
            <p>{selectedItem.openingHours}</p>

            <h5 className='stan fw-bold'>ABOUT</h5>
            <p>{selectedItem.description}</p>

          </div>

        </Modal.Body>
    
      </Modal>
    </div>
  );
};

export default ImageGrid;
