import React, { useState, useEffect } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import AOS from 'aos';
import 'aos/dist/aos.css';

const Nav = () => {

  const [color, setColor] = useState(false);
  const changeColor = () => {
    if (window.scrollY >= 5) {
      setColor(true);
    }else{
      setColor(false);
    }
  } 

  useEffect(() => {
    AOS.init({
        duration: 1500, 
    });

    AOS.refresh(); 
}, []);


  window.addEventListener('scroll', changeColor);


  return (

    <div>
      <nav data-aos="fade" className={`navbar navbar-expand-md p-3 ms-auto me-auto fixed-top ${color ? 'navbar navbar-bg' : 'navbar'}`} style={{fontSize: "1rem"}} >
        <div className="container">

          <a className="navbar-brand ms-2 rasings" href="louvreweb/"  style={{ color:'#F9F2EF '}} > Louvre </a>

          <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
            <span><img src="https://cdn-icons-png.flaticon.com/128/225/225289.png" style={{width: "25px", height: "26px"}}></img> </span>
          </button>

          <div className="collapse navbar-collapse" id="navbarNav">
            <ul className="navbar-nav ms-auto rasings" >
              <li className="nav-item" >
                <Link to="/visit" spy={true} smooth={true} duration={100} className="nav-link " style={{ color: '#F9F2EF '  }} >
                  Visit
                </Link>
              </li>
              <li className="nav-item">
                <Link to="/explore" spy={true} smooth={true} duration={100} className="nav-link" style={{ color: '#F9F2EF ' }} >
                  Explore
                </Link>
              </li>
              {/* <li className="nav-item">
                <Link to="/exhibitions" spy={true} smooth={true} duration={100} className="nav-link" style={{ color: '#F9F2EF ' }} >
                  Exhibitions
                </Link>
              </li> */}

              
            </ul>
          </div>
        </div>
      </nav>
    </div>
  );
};

export default Nav;
